	module.exports = {"data":{"markdownRemark":{"html":"<p>This episode, I sit down with Leslie Sandoval, instructional designer of 11 years, and we talk higher ed and a project supporting faculty with online courses. We’re digging into what is probably a universal issue for higher ed IDs: supporting faculty in the pedagogy and technology necessary to teach online courses and the online portions of hybrid courses. Leslie shares the ups and downs, ins and outs of implementing their first wave solution to tackle this need.</p>\n<iframe src=\"https://simplecast.com/e/59984?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>","frontmatter":{"date":"March 09, 2017","path":"/blog/dearid-episode-39-supporting-faculty-with-online-teaching-with-leslie-sandoval","title":"#DearID Episode 39: Supporting Faculty with Online Teaching with Leslie Sandoval","tags":["collaboration","faculty","higher ed","instructional design","SMEs"],"categories":["Dear ID Show"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":3,"src":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg","srcSet":"/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c7d05.jpg 203w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-784f5.jpg 405w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-c6512.jpg 810w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-adada.jpg 1215w,\n/static/twitter-8f833ed6b7e5464c0e3be8d40e769b35-1c372.jpg 1500w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

